import { useState, useEffect } from 'react'
import NotificationCard from './NotificationCard'
import home from '../../Components/Media/home.png'
import ApiHeader from '../../Components/ApiList/ApiHeader'
import CitizenApplyApiList from '../../Components/CitizenApplyApiList'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify'
import * as yup from 'yup'
import { useFormik } from 'formik'
import { TbSearch } from 'react-icons/tb'
import { FcHome } from 'react-icons/fc'
import Modal from 'react-modal';
import { MdSend } from 'react-icons/md'
import { BiChevronsRight } from 'react-icons/bi'
import CitizenMessageScreen from '../../Components/CitizenMessageScreen'
import { ColorRing } from "react-loader-spinner";
import FormStatusTimeline from '.././Contents/CitizenSafForm/FormStatusTimeline'
import { AiFillInfoCircle } from 'react-icons/ai'
import BarLoader from '../../Components/Common/BarLoader'



const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        backgroundColor: 'transparent',
        border: 'none'
    },
};
Modal.setAppElement('#root');


function PropertyView(props) {

    const [loaderStatus, setloaderStatus] = useState(false)
    const { api_applicationListByModule } = CitizenApplyApiList()
    const [applicationListByModule, setapplicationListByModule] = useState()
    const [propertyApplicationList, setpropertyApplicationList] = useState()
    const [selectedApplicationId, setselectedApplicationId] = useState()
    const [selectedWorkflowId, setselectedWorkflowId] = useState()
    const [show, setshow] = useState(false)
    const [modalIsOpen, setIsOpen] = useState(false);
    const [modalIsOpen2, setIsOpen2] = useState(false);




    function openModal() {
        setIsOpen(true);
    }

    function afterOpenModal() {
        // references are now sync'd and can be accessed.
        // subtitle.style.color = '#f00';
    }

    function closeModal() {
        setIsOpen(false);
    }
    function openModal2() {
        console.log('modal open 2')
        setIsOpen2(true);
    }

    function closeModal2() {
        setIsOpen2(false);
    }

    const navigate = useNavigate()

    let token = window.localStorage.getItem('citizen_token')

    // findholding feature
    const { api_getHoldingDetails } = CitizenApplyApiList()


    const notify = (message) => {
        toast.dismiss(message)
        toast.info(message)
    }

    const [mutationStatus, setmutationStatus] = useState('col-span-12 md:col-span-4 py-6')// to maintain the hide show state of mutation
    const [dataListStatus, setDataListStatus] = useState(false) //to toggle hide show of data list table after finding the holding data from search holding
    const [holdingData, setholdingData] = useState() //to hold found holding data
    const [asstypeStatus, setasstypeStatus] = useState('both') //to hold found holding data

    //function to hide and show and animate assessment options
    const assStatus = (type) => {
        setasstypeStatus(type)

        if (type == 'mu') {
            setmutationStatus('col-span-12 md:col-span-12 py-0 block')
        }
    }

    const validationSchema = yup.object({
        holdingNo: yup.string().required('Enter holding no.')
    })
    const formik = useFormik({
        initialValues: {
            holdingNo: ''
        },

        onSubmit: (values, resetForm) => {
            console.log('propertyaddressdetails ', values)
            findHolding(values)
        }
        , validationSchema
    })

    // let holdingNo = Hol / Ward / 001

    //function to fetch holding data and set into data table
    const findHolding = (values) => {
        // let holdingNo = formik.holdingNo

        let token = window.localStorage.getItem('citizen_token')
        console.log('token at basic details is post method...', token)
        const header = {
            headers:
            {
                Authorization: `Bearer ${token}`,
                Accept: 'application/json',
            }
        }
        const requestBody = {
            "wardId": 50,
            "holdingNo": values.holdingNo

        }
        console.log('form request body....', requestBody)

        // return
        axios.post(`${api_getHoldingDetails}`, requestBody, header)
            .then(function (response) {

                console.log('verified holding no', response.data)
                setholdingData(response.data)


            })
            .catch(function (error) {
                console.log('errorrr....holding ', error);
            })
    }
    console.log("property id in holding...", holdingData?.data?.property_id)
    //findholding feature


    //{////********ApplicationList*******//////}
    const fetchApplicationList = () => {
        setloaderStatus(true)
        let requestBody = {
            module: "Property"
        }
        axios.post(api_applicationListByModule, requestBody,
            {
                headers:
                {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json',
                }
            })
            .then(function (response) {
                console.log("apllication list holding----- ", response);
                setapplicationListByModule(response.data.data?.Property)
                setloaderStatus(false)
            })
            .catch(function (error) {
                console.log('error in application list', error);
                setloaderStatus(false)
            })

    }
    console.log("application list by module id...", applicationListByModule)


    //{////********Property ApplicationList  by holding*******//////}
    const fetchPropertyListByHolding = () => {
        setloaderStatus(true)
        let requestBody = {
            module: "Holding"
        }
        axios.post(api_applicationListByModule, requestBody,
            {
                headers:
                {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json',
                }
            })
            .then(function (response) {
                console.log("property list by holding==----- ", response);
                setpropertyApplicationList(response.data.data?.Holding?.applications)
                setloaderStatus(false)

            })
            .catch(function (error) {
                console.log('error in holding fetch ', error);
                setloaderStatus(false)
            })

    }


    useEffect(() => {
        setloaderStatus(true)
        fetchApplicationList();
        fetchPropertyListByHolding();

    }, [])


    return (
        <>
            {/* {loaderStatus && (
                <div className="w-full z-10 absolute mx-auto text-center flex justify-center items-center top-1/2">
                    <span className="inline">
                        <ColorRing
                            visible={true}
                            height="120"
                            width="120"
                            ariaLabel="blocks-loading"
                            wrapperStyle={{}}
                            wrapperClass="blocks-wrapper"
                            colors={["#e15b64", "#f47e60", "#f8b26a", "#abbd81", "#849b87"]}
                        />
                    </span>
                </div>
            )} */}
            {
                loaderStatus && <BarLoader/>
            }
            <div className='grid grid-cols-12 mt-4 md:mt-4  md:bg-white md:shadow-lg px-10 border-spacing-x-10'>
                <div className='md:px-2 col-span-12 md:col-span-9 pt-4'>
                    <div className='md:px-2 w-full'>
                        <div className='text-sm  font-sans text-gray-700 w-full grid grid-cols-12'><div className='col-span-12 md:col-span-8 font-semibold text-xl md:text-2xl text-gray-600 font-serif'><img className='inline w-8 border-4 shadow-lg border-white rounded-full' src={home} />Your Property Information</div>
                            <div className='col-span-12 md:col-span-4 text-right hidden md:block'>
                                <button className=' border border-sky-400 text-sky-400 cursor-pointer px-2 py-2 shadow-lg hover:scale-105 hover:bg-sky-400 hover:text-white rounded-sm' onClick={() => navigate('/safform/new/0')}>Apply Holding</button>
                                <button onClick={openModal} className=' border border-sky-400 text-sky-400 cursor-pointer px-2 py-2 shadow-lg  hover:scale-105 hover:bg-sky-400 hover:text-white rounded-sm ml-4'>Apply Mutation</button>
                            </div>
                        </div>


                        {applicationListByModule?.SAF != '' && applicationListByModule?.SAF?.length != 0 && <div className='text-sm md:text-lg font-sans font-semibold text-gray-700 mt-4 mb-2 md:mt-10'>Pending Applications</div>}

                        {/* application card */}
                        {/* remove filter in production */}
                        {applicationListByModule?.SAF != '' && applicationListByModule?.SAF?.map((application) => (
                            <>
                                <div className='w-full pb-0 pt-6 bg-white rounded-lg border border-gray-100 shadow-xl mb-6 relative overflow-hidden'>
                                    <div className='w-full pb-4 pt-6 px-4'>
                                        {/* <span className='px-2 bg-gray-400 text-white text-xs absolute top-0 left-0 shadow-lg'>{application?.assessment_type == 1 && "New-Assessment"} {application?.assessment_type == 2 && "Re-Assessment"} {application?.assessment_type == 3 && "Mutation"}</span> */}
                                        <span className='px-2 bg-sky-200 text-gray-700 text-xs absolute top-0 left-0 shadow-lg'>Form submit is pending</span>
                                        <div className="flex mt-0 mb-5">
                                            <FormStatusTimeline active={1 == 1 && true} index="1" level="Form Submit" verificationStatus={3 >= 2 && true} last={false} />
                                            <FormStatusTimeline active={1 == 2 && true} index="2" level="Document Upload" verificationStatus={application?.payment_status == 0 ?false: true} last={false} />
                                            <FormStatusTimeline active={2 == 3 && true} index="3" level="Payment Done" verificationStatus={application?.doc_upload_status == 0 ?false: true} last={true} />
                                        </div>
                                        <div className='w-full py-4 rounded-lg  space-b-2'>
                                            <div className='w-full  grid grid-cols-12'>
                                                <div className='col-span-12 md:col-span-6 grid grid-cols-12'>
                                                    <div className='col-span-6'>
                                                        <div className='font-semibold text-gray-700'>{application?.applicant_name==''?'N/A':application?.applicant_name}</div>
                                                        <div className='text-xs text-gray-600 font-serif italic'>Applicant Name</div>
                                                    </div>

                                                    <div className='col-span-6'>
                                                        <div className='font-semibold text-gray-700'>{application?.application_date==''?'N/A':application?.application_date}</div>
                                                        <div className='text-xs text-gray-600 font-serif italic'>Apply Date</div>
                                                    </div>
                                                </div>
                                                <div className='col-span-12 md:col-span-6 grid grid-cols-12 mt-4 md:mt-0'>
                                                    <div className='col-span-6'>
                                                        <div className='font-semibold text-gray-700'>{application?.saf_no==''?'N/A':application?.saf_no}</div>
                                                        <div className='text-xs text-gray-600 font-serif italic'>Saf/No</div>
                                                    </div>

                                                    <div className='col-span-6'>
                                                        <div className='font-semibold text-gray-700'>{application?.assessment_type}</div>
                                                        <div className='text-xs text-gray-600 font-serif italic'>Application Type</div>
                                                    </div>
                                                </div>
                                            </div>
                                            
                                            <hr className='h-4 mt-10 mb-4' />
                                            <div className='w-full md:w-1/2 pl-2 text-xs'>
                                                <div className='font-bold font-sans mb-4'>Actions</div>
                                                {application?.payment_status == 0 && <div className='text-xs text-amber-500'> <AiFillInfoCircle className="inline" /> Payment is pending <span className='float-right ml-4 cursor-pointer bg-sky-400 text-white px-4 py-1 text-xs shadow-lg hover:bg-sky-600 hover:scale-105 rounded-sm border border-white' onClick={() => navigate(`/viewDemand/${application?.application_id}`)}> &nbsp;&nbsp;Pay&nbsp;&nbsp;&nbsp;</span></div>}
                                                {application?.doc_upload_status == 0 && <div className='text-xs text-amber-500 mt-4'> <AiFillInfoCircle className="inline" /> Document is pending <span className='float-right ml-4 cursor-pointer bg-sky-400 text-white px-4 py-1 text-xs shadow-lg hover:bg-sky-600 hover:scale-105 rounded-sm border border-white' onClick={() => navigate(`/safdocumentupload/${application?.application_id}`)}>Upload</span></div>}
                                            </div>
                                        </div>
                                        <span onClick={() => navigate(`/propApplicationDetails/${application?.application_id}`)} className='px-2 text-white text-xs absolute top-2 right-2  font-semibold py-1 rounded-lg border-2 bg-sky-400 border-sky-400 hover:border-sky-500 hover:bg-sky-600 hover:text-white shadow-xl hover:scale-105 cursor-pointer'>View <BiChevronsRight className="inline font-semibold text-xl" /></span>
                                    </div>
                                    <div className='w-full grid grid-cols-12 bg-sky-400 py-4 px-4'>
                                        <div className="col-span-12 md:col-span-6 text-center md:text-left">
                                            <div className='text-white'>Contact with us.</div>
                                        </div>
                                        <div className="col-span-12 md:col-span-6 text-center md:text-right mt-5 md:mt-0">
                                            <button onClick={() => {
                                                setselectedApplicationId(application?.application_id)
                                                setselectedWorkflowId(application?.workflow_id)
                                                openModal2()
                                            }} className='bg-white py-3 md:py-2 px-4 md:px-2 text-sky-400 rounded-lg shadow-lg hover:scale-105 '>
                                                Send Message <MdSend className="inline" />
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </>
                        ))}
                    </div>
                    {/* Property holding */}
                    {/* remove filter in production */}
                    <div className='px-2 w-full mt-10'>
                        {propertyApplicationList != '' && propertyApplicationList?.length != 0 &&
                            <div className='text-lg font-sans font-semibold text-gray-700'>Property</div>
                        }

                        {propertyApplicationList != '' && propertyApplicationList?.map((property, index) => (
                            <div className='bg-white shadow-lg pb-2  mb-2 rounded-lg relative'>
                                {/* application card */}
                                <div className='w-full py-4 rounded-lg  px-4 md:flex'>
                                    <div className='w-full md:w-3/4 border-r border-gray-600 flex space-x-2'>
                                        <div>
                                            <div className='font-semibold text-gray-700'>{property?.owner_name}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>First Owner</div>
                                        </div>
                                        <div>
                                            <div className='font-semibold text-gray-700'>{property?.new_holding_no}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Holding No.</div>
                                        </div>
                                        <div className='pl-4'>
                                            <div className='font-semibold text-gray-700'>{property?.leftamount==''?'N/A':property?.leftamount}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Total Tax</div>
                                        </div>
                                        <div className='pl-4'>
                                            <div className='font-semibold text-gray-700'>{property?.lastpaidamount==''?'N/A':property?.lastpaidamount}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Last Paid</div>
                                        </div>
                                        <div className='pl-4'>
                                            <div className='font-semibold text-gray-700'>{property?.lastpaiddate==''?'N/A':property?.lastpaiddate}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Last Paid Date</div>
                                        </div>
                                    </div>

                                    <div className='block md:hidden w-full h-1 border-b-2 border-white mt-4'></div>
                                    <div className='w-full md:w-1/4 pl-2 text-xs'>
                                        <div className='font-semibold font-sans'>Actions</div>
                                        {/* <div className='text-xs text-amber-500'> Application is pending</div>
                                        <div className='text-xs text-amber-500'> Waiting to upload document</div> */}
                                    </div>

                                </div>
                                <div className='w-full flex space-x-4  flex-wrap pl-4'>
                                    <div onClick={() => navigate(`/safform/re/${property?.prop_id}`)} className='flex-initial text-xs border border-amber-500 text-amber-500 px-2 py-1 shadow-sm rounded-lg cursor-pointer shadow-2xl shadow-gray-400 hover:bg-amber-600 hover:text-white hover:scale-105'>Re-assessment</div>
                                    <div onClick={() => navigate(`/safform/mu/${property?.prop_id}`)} className='flex-initial text-xs border border-amber-500 text-amber-500 px-2 py-1 shadow-sm rounded-lg cursor-pointer shadow-2xl shadow-gray-400 hover:bg-amber-600 hover:text-white hover:scale-105'>Mutation</div>
                                    <div onClick={() => navigate(`/con-form/${property?.prop_id}`)} className='flex-initial text-xs border border-amber-500 text-amber-500 px-2 py-1 shadow-sm rounded-lg cursor-pointer shadow-2xl shadow-gray-400 hover:bg-amber-600 hover:text-white hover:scale-105'>Concession</div>
                                    <div onClick={() => navigate(`/safform/waterHarvesting/${property?.prop_id}`)} className='flex-initial text-xs border border-amber-500 text-amber-500 px-2 py-1 shadow-sm rounded-lg cursor-pointer shadow-2xl shadow-gray-400 hover:bg-amber-600 hover:text-white hover:scale-105'>RainWater-Harvesting</div>
                                    <div onClick={() => navigate(`/objection/${property?.prop_id}`)} className='flex-initial text-xs border border-amber-500 text-amber-500 px-2 py-1 shadow-sm rounded-lg cursor-pointer shadow-2xl shadow-gray-400 hover:bg-amber-600 hover:text-white hover:scale-105'>Objection</div>
                                </div>
                                <span onClick={() => navigate(`/holdingPropertyDetails/${property?.prop_id}`)} className='px-2 text-white text-xs absolute bottom-2 right-2  font-semibold py-1 rounded-lg border-2 bg-sky-400 border-sky-400 hover:border-sky-500 hover:bg-sky-600 hover:text-white shadow-xl hover:scale-105 cursor-pointer'>View <BiChevronsRight className="inline font-semibold text-xl" /></span>
                            </div>
                        ))}
                        <div className='w-full h-40'></div>
                    </div>

                    {(propertyApplicationList?.length == 0 && applicationListByModule?.SAF?.length == 0) && <div className='px-20'>
                        <div><img className='w-56 mx-auto' src={home} /></div>
                        <div className='text-2xl md:text-3xl font-semibold text-gray-700 text-center font-serif mt-0'>Click Apply Holding for new Holding application</div>
                        <div className='text-center mt-4 text-ellipsis overflow-hidden h-40 md:h-auto opacity-80 text-xl'> All your property and applied applications will be visible here, like your new holding application, re-assessment appliation, mutation and other property related applications.</div>
                    </div>}
                </div>


                <div className='col-span-12 md:col-span-3 pt-4 border-l-8 border-white mt-10 md:mt-0 hidden md:block relative'>
                    <NotificationCard />
                </div>
            </div>

            {/* find holding modal */}
            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <>

                    <div className=''>
                        <div className=' z-50 bg-white opacity-95 '>

                            <section className={`${(mutationStatus == 'col-span-12 md:col-span-12 py-0 block') ? 'block' : 'block'} w-11/12 md:w-9/12 mx-auto mt-0 text-gray-600  body-font overflow-hidden   `}>
                                <div className='flex'>
                                    <div className='flex-1'>
                                        <h1 className="text-gray-900 text-xl title-font font-medium mb-4 flex items-center"><FcHome className="inline" />
                                            <p className='ml-3 '>Find Holding</p>
                                        </h1>
                                    </div>
                                    <div className='flex-1 '>
                                        <button className='float-right hover:scale-105' onClick={closeModal}>
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" class="w-8 h-8 text-red-500">
                                                <path fill-rule="evenodd" d="M12 2.25c-5.385 0-9.75 4.365-9.75 9.75s4.365 9.75 9.75 9.75 9.75-4.365 9.75-9.75S17.385 2.25 12 2.25zm-1.72 6.97a.75.75 0 10-1.06 1.06L10.94 12l-1.72 1.72a.75.75 0 101.06 1.06L12 13.06l1.72 1.72a.75.75 0 101.06-1.06L13.06 12l1.72-1.72a.75.75 0 10-1.06-1.06L12 10.94l-1.72-1.72z" clip-rule="evenodd" />
                                            </svg>
                                        </button>
                                    </div>
                                </div>

                                <form onSubmit={formik.handleSubmit}>
                                    <div className="  grid grid-cols-6">
                                        <div className=" flex-1 form-group mb-3 col-span-4 md:col-span-2">
                                            <label className="form-label inline-block mb-1 text-gray-900 text-sm font-semibold "><small className="block mt-1 text-sm font-semibold text-red-600 inline  ">*</small>Holding no.</label>
                                            <input {...formik.getFieldProps('holdingNo')} type="text" className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none placeholder-gray-300 shadow-md"
                                                placeholder="Enter holding no." />
                                            <span classNameName="text-red-600 absolute text-xs">{formik.touched.holdingNo && formik.errors.holdingNo ? formik.errors.holdingNo : null}</span>
                                        </div>
                                        <div className="flex-1 form-group mb-2 col-span-1 md:col-span-1 align-bottom">
                                            <label className="form-label inline-block mb-1 text-gray-900 text-sm font-semibold invisible "><small className="block mt-1 text-sm font-semibold text-red-600 inline  ">*</small>Enter Holding no.</label>
                                            <div className='md:px-10 text-right'>
                                                <button onClick={() => setDataListStatus(true)} type="submit" className=" px-6 py-2 bg-green-600 text-white font-medium text-xs leading-tight  rounded  hover:bg-green-700 hover:shadow-lg focus:bg-green-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-green-800 active:shadow-lg transition duration-150 ease-in-out"><TbSearch className="inline text-sm font-bold" />Find</button>
                                            </div>
                                        </div>
                                    </div>
                                </form>


                                {/* found holding */}
                                <ToastContainer />
                                {
                                    dataListStatus && <div className="py-2 overflow-x-auto bg-white">
                                        <div className="inline-block min-w-full rounded-lg overflow-hidden bg-white">

                                            <table className="min-w-full leading-normal">
                                                <thead className='font-bold text-left text-sm bg-sky-300'>
                                                    <tr >
                                                        <th className="px-2 py-3 border-b border-gray-200 text-gray-900  text-left text-xs uppercase text-left">Holding No</th>
                                                        <th className="px-2 py-3 border-b border-gray-200 text-gray-900  text-left text-xs uppercase text-left">Owner Name</th>
                                                        <th className="px-2 py-3 border-b border-gray-200 text-gray-900  text-left text-xs uppercase text-left">Guardian Name</th>
                                                        <th className="px-2 py-3 border-b border-gray-200 text-gray-900  text-left text-xs uppercase text-left">Address</th>
                                                        <th className="px-2 py-3 border-b border-gray-200 text-gray-900  text-left text-xs uppercase text-left">Action</th>


                                                    </tr>
                                                </thead>

                                                <tbody className="text-sm">
                                                    <tr className="bg-white shadow-lg border-b border-gray-200">
                                                        <td className="px-2 py-2 text-sm text-left text-gray-900 font-bold">{holdingData?.data?.holding_no}</td>
                                                        {holdingData?.data?.owners?.map((items) => (
                                                            <>
                                                                <td className="px-2 py-2 text-sm text-left text-gray-900 font-bold">{items?.owner_name}</td>
                                                                <td className="px-2 py-2 text-sm text-left text-gray-900 font-bold">{items?.guardian_name}</td>

                                                            </>
                                                        ))}
                                                        <td className="px-2 py-2 text-sm text-left text-gray-900 font-bold">{holdingData?.data?.prop_address}</td>
                                                        <td className="px-2 py-2 text-sm text-left" onClick={() => notify()}>
                                                            {(asstypeStatus == 'mu' || asstypeStatus == 'both') && <button onClick={() => navigate(`/safform/mu/${holdingData?.data?.property_id}`)} type="button" className="ml-2 px-2 py-1 bg-orange-300 text-black font-bold hover:text-white text-xs leading-tight  rounded  hover:bg-orange-700 hover:shadow-lg focus:bg-orange-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-orange-800 active:shadow-lg transition duration-150 ease-in-out">Mutation</button>}
                                                        </td>

                                                    </tr>
                                                </tbody>
                                            </table>

                                        </div>
                                    </div>
                                }
                            </section>

                        </div>
                    </div>


                </>
            </Modal>

            <Modal
                isOpen={modalIsOpen2}
                onRequestClose={closeModal2}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <div className='w-96 h-96'>
                    <CitizenMessageScreen notify={notify} closeModalFun={closeModal2} applicationId={selectedApplicationId} workflowId={selectedWorkflowId} />
                </div>
            </Modal>

            <div className="h-20 w-full"></div>

        </>
    )
}

export default PropertyView